@import url(//db.onlinewebfonts.com/c/b158925a1d762313849f2482a415369d?family=Eina03-SemiBold);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0px;
  padding: 0px;
  background: #f7f7f7;
  height: 100vh;
}

.body-container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 30px;
}
/*# sourceMappingURL=App.css.map */
.section-menu {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 108px;
  background: white;
  box-shadow: 0px 2px 3px 2px rgba(152, 152, 152, 0.25);
  border-radius: 4px;
  font-size: 18px;
  top: 0px;
  z-index: 999;
  font-family: Eina03-SemiBold;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.container {
  overflow: hidden;
  width: 1091px;
  z-index: 1;
  max-width: 1300px;
}

.ul-control {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 469px;
  height: 60px;
  margin-left: 15px;
}

.underline-grey {
  position: absolute;
  top: 81px;
  border-radius: 3px;
  width: 467px;
  height: 2px;
  opacity: 0.6;
  background: #E6E9ED;
}

/*Button style*/
.dine-out-button, .reservation-button, .pick-up-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 147px;
  height: 58.98px;
  background-color: white;
  border: none;
  border-radius: 0px;
  font-family: Eina03-SemiBold;
  line-height: 21px;
  font-size: 18px;
  text-decoration: none;
  color: black;
  cursor: pointer;
  transition: background .2s ease;
}

.dine-out-button, .reservation-button, .pick-up-button:focus {
  outline: none;
}

.reservation-button:hover, .dine-out-button:hover, .pick-up-button:hover {
  transform: translateY(-2px);
}

.img-space {
  margin: 5px;
}

.active {
  position: relative;
  z-index: 1;
  background-color: #8e97d4;
  border-bottom: 3px solid #5B6BC8;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  color: white;
}

.nav-link2 {
  margin-left: 15px;
  margin-right: 15px;
}

.navbar-menu {
  display: flex;
  justify-content: center;
  align-items: center;
}

.ul-control {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  list-style: none;
}
